import axios from 'axios'
import qs from 'qs'
import _this from '../main.js';


// axios 
// axios.defaults.timeout = 8000;
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded;charset=UTF-8';
axios.defaults.headers.post['x-requested-with'] = 'xmlhttprequest'; //ajax
axios.defaults.headers.get['x-requested-with'] = 'xmlhttprequest'; //ajax
axios.defaults.headers.common['ZT-Mobile'] = 'weixin';

axios.defaults.baseURL = 'http://rugao.ztemap.com:8086/api';

// post
axios.interceptors.request.use((config) => {
  if (config.method === 'post') {
    config.data = qs.stringify(config.data)
  }
  return config;
}, (error) => {
  _this.$vux.toast.text('', 'top', '800')
  return Promise.resolve(error);
});

// 
axios.interceptors.response.use(response => {
  return response
}, error => {
  if (error && error.response) {
    switch (error.response.status) {
      case 400:
        error.message = ''
        break

      case 401:
        error.message = ''
        break

      case 403:
        error.message = ''
        break

      case 404:
        error.message = `: ${err.response.config.url}`
        break

      case 408:
        error.message = ''
        break

      case 500:
        error.message = ''
        break

      case 501:
        error.message = ''
        break

      case 502:
        error.message = ''
        break

      case 503:
        error.message = ''
        break

      case 504:
        error.message = ''
        break

      case 505:
        error.message = 'HTTP'
        break

      default:
    }
  }
  _this.$vux.toast.text(error.message, 'top', '800')
  return Promise.resolve(error);
})

export function fetch(url, params, method, config) {
  return new Promise((resolve, reject) => {
    if (method == 'get') {
      axios({
          url: url,
          params: params,
          method: method
        }).then(response => {
          resolve(response.data);
        }, err => {
          reject(err);
        })
        .catch((error) => {
          reject(error);
        })
    } else if (method == 'post') {
      axios.post(url, params, config).then(response => {
          resolve(response.data);
        }, err => {
          reject(err);
        })
        .catch((error) => {
          reject(error)
        })
    }
  })
}

